      h2 3.3. Seguridad y conservación del estado físico del usuario

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p La seguridad del usuario durante un recorrido turístico constituye un componente esencial del servicio, ya que no solo implica la prevención de riesgos físicos, sino también el cuidado integral del bienestar emocional, cultural y social del visitante. Este principio se fundamenta en el respeto por los entornos naturales, las tradiciones y las prácticas de las comunidades anfitrionas.
      p La seguridad, además de ser un derecho fundamental, representa una condición no negociable para la operación turística responsable.
      p En los contextos étnicos y sostenibles, su alcance se amplía para incluir el bienestar espiritual, la integridad cultural y la protección de los sistemas de conocimiento tradicional que forman parte del patrimonio vivo de los pueblos. Según la Organización Mundial del Turismo (OMT, 2020), la seguridad es un elemento fundamental de la calidad del servicio turístico, ya que fortalece la confianza del visitante y contribuye a la sostenibilidad del destino. En este sentido, el guía turístico tiene la responsabilidad de aplicar medidas preventivas, identificar posibles peligros y garantizar que las actividades se desarrollen en condiciones controladas.
