    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p La seguridad en el turismo no se limita al ámbito físico; también abarca dimensiones culturales y espirituales. En este sentido, se debe proteger a los visitantes de prácticas que puedan afectarlos emocional o espiritualmente, como participar en ceremonias sin la orientación o preparación adecuada; y, de manera recíproca, salvaguardar a las comunidades frente a acciones irrespetuosas, tales como la toma de fotografías sin autorización, la invasión de espacios sagrados o la apropiación cultural indebida. Estas acciones deben regirse por la Ley 2121 de 2021 (Ley de Guías de Turismo) y los protocolos del Sistema de Gestión de Seguridad y Salud en el Trabajo (SG-SST), los cuales deben aplicarse también a los turistas.
